.custom-timeline .react-calendar-timeline .rct-top-header .rct-label-group {
  background-color: #e0e1ff;
  color: #4447ab;
  font-weight: bold;
}

.custom-timeline .react-calendar-timeline .rct-bottom-header .rct-label {
  background-color: #ecedfc;
}

.custom-timeline .react-calendar-timeline .rct-hl-odd, .custom-timeline .react-calendar-timeline .rct-sidebar-row.rct-sidebar-row-odd {
  background-color: rgba(236, 237, 252, 0.5);
}

.custom-top-left-table {
  position: relative;
}

.custom-top-left-table {
  width: 150px;
  height: 80px;
  background-color: #e7e8fb;
  position: relative;
  color: #4447ab;
}

.custom-top-left-table:before, .custom-top-left-table:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  border-color: transparent;
  border-style: solid;
}

.custom-top-left-table:after {
  border-width: 80px 0 0 150px;
  border-left-color: #e0e1ff;
  border-bottom-color: #e0e1ff;
}

.custom-top-left-table span {
  position: absolute;
  top: 5px;
  right: 10px;
}

.custom-top-left-table div {
  position: absolute;
  bottom: 5px;
  left: 10px;
  z-index: 1;
}