.custom-dropdown {
  position: relative;
  display: inline-block;
  width: 100%;
  border-radius: 5px;
  background-color: #fff;
}

.dropbtn {
  text-align: left;
  padding-left: 10px;
  color: #999999;
  font-size: 16px;
  cursor: pointer;
  border-style: solid;
  border-width: 1px;
  border-radius: 5px;
  border-color: #d0d0d0;
  height: 32px;
  width: 100%;
  background-color: transparent;
}

.dropbtn.disabled {
  background-color: #f2f2f2;
}

.dropbtn.has-error {
  border: solid 1px red;
}

.dropbtn span {
  width: 80%;
  margin-top: 2px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  display: block;
  text-overflow: ellipsis;
}

.dropbtn:hover, .dropbtn:focus {
  border-color: #66ccff;
}

.custom-dropdown input {
  font-size: 14px;
  background-repeat: no-repeat;
  padding: 5px 5px 5px 10px;
  border: none;
  border-bottom: 1px solid #ddd;
  width: 100%;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 200px;
  overflow: hidden;
  border: 1px solid #ddd;
  z-index: 1;
}

.dropdown-content .list {
  height: 200px;
  overflow-y: auto;
  width: 100%;
  border: none;
}

.dropdown-content option {
  cursor: pointer;
  color: black;
  padding: 5px 5px 5px 10px;
  text-decoration: none;
  display: block;
  font-weight: normal;
  font-size: 14px;
  border: none;
}

.dropdown-content option:hover {
  background-color: #e6f7ff;
}

.dropdown-content.show {
  display: block;
}

.custom-dropdown .icon {
  display: inline;
  margin: 7px 5px 0 -25px;
}