.tabs-button {
  .ant-tabs-bar {
    border-bottom: unset;
    margin-bottom: unset;
    .ant-tabs-nav {
      border: 1px solid $light;
      box-shadow: $box-shadow-sm;
      margin-bottom: 1rem;
      border-radius: 0.5rem;
      overflow: hidden;
      .ant-tabs-tab {
        margin-right: unset;
        background-color: white;
        @include button-size(
          $btn-padding-y,
          $btn-padding-x,
          $font-size-base,
          $btn-line-height,
          unset
        );
        &:hover {
          color: $success;
        }
        &-active {
          background-color: $success;
          color: white;
          &:hover {
            color: white;
          }
        }
      }
      .ant-tabs-ink-bar {
        display: none !important;
      }
    }
  }
}
