.ant-table {
  .table {
    &-primary {
      background-color: $th-primary !important;
      color: rgba(black, 0.75) !important;
      &-light {
        background-color: $th-primary-light !important;
        color: rgba(black, 0.75) !important;
      }
    }
    &-success {
      background-color: $th-success !important;
      color: rgba(black, 0.75) !important;
      &-light {
        background-color: $th-success-light !important;
        color: rgba(black, 0.75) !important;
      }
    }
  }
}
