.ant-carousel .slick-slide {
  text-align: center;
  height: auto;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: red;
}

.ant-carousel .slick-slider .slick-dots li button{
  background: #4447ab;
  width: 15px;
  height: 15px;
  margin: 2px;
  margin-top: 10px;
  border-radius: 10px;
}